.wxdc-order-detail-pic {
  width: 70px;
  height: 70px;
  border-radius: 100%;
  display: block;
  text-align: center;
  margin: auto;
  position: fixed;
  left: 0;
  right: 0;
  top: 20px;
  z-index: 200000;
  border: 3px solid #fff;
}
.wxdc-order-detail-list .wxdc_weui_media_title {
  padding-left: 15px;
  padding-right: 15px;
}
/* button */
.wxdc-blue {
  background: #26a0ff;
  color: #fff;
}
.wxdc-blue:active {
  background: #3199e8 !important;
  color: #ddd !important;
}
.wxdc-blue:after {
  display: none;
}



