





























































































.discuss_list {
  margin: 0;
  font-size: 16px
}
.discuss_item {
  position: relative;
  padding:10px 10px 10px 45px;
  background: rgba(255,255,255,0.9);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  margin: 5px;
  border-radius: 10px;
  border: 1px solid #d9d9d9;
}
.discuss_item:after {
  content: "\200B";
  display: block;
  height: 0;
  clear: both
}
.discuss_item .user_info {
  min-height: 20px;
  overflow: hidden
}
.discuss_item .nickname {
  display: block;
  font-weight: 400;
  font-style: normal;
  color: #727272;
  width: 9em;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal
}
.discuss_item .avatar {
  position: absolute;
  left: 5px;
  top: 15px;
  width: 35px;
  height: 35px;
  background-color: #ccc;
  vertical-align: top;
  margin-top: 0;
  border-radius: 2px;
}
.discuss_item .discuss_message {
  color: #3e3e3e;
  line-height: 1.5
}
.discuss_item .discuss_extra_info {
  color: #8c8c8c;
  font-size: 12px
}
.discuss_item .discuss_extra_info a {
  margin-left: .5em
}
.reply_result {
  position: relative;
  margin-top: .5em;
  padding-top: .5em;
  padding-left: .4em
}
.reply_result:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  border-top: 1px solid #dadada;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5)
}
.reply_result .nickname {
  position: relative;
  overflow: visible
}
.reply_result .nickname:before {
  content: " ";
  position: absolute;
  left: -0.4em;
  top: 50%;
  margin-top: -7px;
  width: 3px;
  height: 14px;
  background-color: #26a2ff;
}



