.expand-transition {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  height: 135px;
  background-color: #eee;
  overflow: hidden;
}
/* .expand-enter  */
/* .expand-leave  */
.expand-enter, .expand-leave {
  height: 0;
  opacity: 0;
}
.expandmenu-transition {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  height: 270px;
  background-color: #eee;
  overflow: hidden;
}
.expandmenu-enter, .expandmenu-leave {
  height: 0;
  opacity: 0;
}
.day {
  background-color:#000;
  color:#fff;
  text-align:center;
  display:inline-block;
  padding:0 3px;
  border-radius:3px;
  margin: 0 0 0 -2px;
}
/*  */
.wxdc-list-title {
  font-size: 14px;
  color: #888;
  padding-left: 10px;
}
.wxdc-list-title .weui_cell_bd > p {
  color: #888;
  padding-left: 3px;
}
.wxdc-list-title.weui_cell:first-child:before {
  display: block;
  bottom: 0;
  top: auto;
  left: 10px;
}
/*  */
.box2 {
  margin-left: -5px;
}
.wxdc_weui_media_title {
  padding: 7px 0;
  font-size: 13px;
}
.wxdc_weui_media_title .weui_cell_ft {
  white-space: nowrap;
}
.wxdc_weui_media_qsf {
  background-color: #26a2ff;
  color: #fff;
  border-radius: 3px;
  display: inline-block;
  padding: 1px 2px;
  border: 1px solid transparent;
  font-size: 10px;
}
.wxdc_weui_media_psf {
  color: #26a2ff;
  border: 1px solid #26a2ff;
  padding: 0 2px 1px;
  border-radius: 3px;
  white-space: nowrap;
  font-size: 10px;
}
.wxdc_weui_media_title_noborder:before {
  display: none;
}
/*  */
.wxdc-cond {
  position: relative;
}
.wxdc-cond:before {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  border-top: 1px solid #D9D9D9;
  color: #D9D9D9;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.wxdc-cond-item {
  text-align: center;
  color: #555;
  font-size: 14px;
  padding: 12px 0;
  position: relative;
}
.wxdc-cond-item:after {
  content: " ";
  position: absolute;
  top: 10px;
  bottom: 10px;
  right: 0;
  width: 1px;
  border-left: 1px solid #D9D9D9;
  color: #D9D9D9;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleX(0.5);
          transform: scaleX(0.5);
}
.wxdc-cond-item > span {
  margin-left: 3px;
}



